import React, { Component } from 'react';

import { Table } from 'antd';


const columns = [{
    title: 'class',
    dataIndex: 'class',
    key: 'class',
    // render: text => <a href="javascript:;">{text}</a>,
  }, {
    title: 'Train',
    dataIndex: 'Train',
    key: 'Train',
  }, {
    title: 'Test',
    dataIndex: 'Test',
    key: 'Test',
  }];

const data = [{
    key: '1',
    class: '',
    Train: 3,
    Test: 2,
  }, {
    key: '2',
    class: '',
    Train: 44,
    Test: 32,
  }, {
    key: '3',
    class: '',
    Train: 57,
    Test: 25,
  }, {
    key: '4',
    class: '',
    Train: 70,
    Test: 30,
  }, {
    key: '5',
    class: '',
    Train: 13,
    Test: 8,
  }, {
    key: '6',
    class: '',
    Train: 6,
    Test: 5,
  }, {
    key: '7',
    class: '',
    Train: 46,
    Test: 24,
  }, {
    key: '8',
    class: '',
    Train: 41,
    Test: 15,
  }, {
    key: '9',
    class: '',
    Train: 36,
    Test: 24,
  }, {
    key: '10',
    class: '',
    Train: 67,
    Test: 35,
  }, {
    key: '11',
    class: '',
    Train: 383,
    Test: 200,
  }];
  
class DataTable extends Component {
    render() {
        return (
        <Table pagination={false} columns={columns} dataSource={data} />
        )
    }
  }
  
export default DataTable;



